* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: #000000;
  font-family: 'Orbitron', sans-serif;
    display: flex;
  justify-content: center;
  align-items: center;
        width: 100%;
      height: 100vh;
}


//  btn6
.btn-group6 {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
}
.btn6 {
  position: relative;
  display: inline-block;
  padding: 15px 30px;
  color: #2196f3;
  text-transform: uppercase;
  letter-spacing: 4px;
  text-decoration: none;
  font-size: 20px;
  overflow: hidden;
  transition: 0.2s;
  &:hover {
    color: #000000;
    background-color: #ffffff;
    box-shadow: 0 0 10px #ffffff,
                0 0 40px #ffffff,
                0 0 80px #ffffff;
    transition-delay: 1.2s;
    & span {
      &:nth-child(1) {
        left: 100%;
        transition: 1s;
      }
      &:nth-child(2) {
        top: 100%;
        transition: .5s;
        transition-delay: 0.25s;
      }
      &:nth-child(3) {
        right: 100%;
        transition: 1s;
        transition-delay: 0.5s;
      }
      &:nth-child(4) {
        bottom: 100%;
        transition: .5s;
        transition-delay: 0.75s;
      }
    }
  }
  & span {
    position: absolute;
    display: block;
    &:nth-child(1) {
      top: 0;
      left: -100%;
      width: 100%;
      height: 2px;
      background: linear-gradient(90deg, transparent, #3300ff);
    }
    &:nth-child(2) {
      top: -100%;
      right: 0;
      width: 2px;
      height: 100%;
      background: linear-gradient(360deg, transparent, #3300ff);
    }
    &:nth-child(3) {
      bottom: 0;
      right: -100%;
      width: 100%;
      height: 2px;
      background: linear-gradient(270deg, transparent, #2196f3);
    }
    &:nth-child(4) {
      bottom: -100%;
      left: 0;
      width: 2px;
      height: 100%;
      background: linear-gradient(180deg, transparent, #2196f3);
    }
  }
}

